<template>
	<div class="records">
    <el-card class="box-card pc">
      <div slot="header" class="clearfix">
        <span style="font-size: 18px;"></span>
      </div>
      <el-table
        v-if="tableData.length > 0"
        :data="tableData"
        stripe
        style="width: 100%">
        <el-table-column
          prop="name"
          label=""
          width="130">
        </el-table-column>  
        <el-table-column
          prop="created_at"
          label=""
          width="210">
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button
              class="gray"
              size="mini"
              @click="getAnswer(scope.row.id)"></el-button><el-button
              size="mini"
              type="success"
              @click="getReport(scope.row.id)"></el-button><el-button
              size="mini"
              type="danger"
              @click="getInfo(scope.row.id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <el-card class="box-card mobile">
      <div slot="header" class="clearfix">
        <span style="font-size: 18px;"></span>
      </div>
      <el-table
        v-if="tableData.length > 0"
        :data="tableData"
        stripe
        style="width: 100%">
        <el-table-column
          prop="name"
          label="">
        </el-table-column>  
        <el-table-column
          prop="created_at"
          label="">
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button
              class="gray"
              size="mini"
              @click="getAnswer(scope.row.id)"></el-button><el-button
              size="mini"
              type="success"
              @click="getReport(scope.row.id)"></el-button><el-button
              size="mini"
              type="danger"
              @click="getInfo(scope.row.id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-dialog :title="answer.paper_name" :visible.sync="answerDetailVisible">
      <div style="border-bottom: 1px solid #ccc;padding-bottom: 20px;margin-bottom: 20px;" v-for="(item,index) in answer.items" key="index">
        <h2 style="font-size: 18px;margin: 0 0 10px;">{{item.title}}</h2>
        <div style="font-size: 16px;">{{item.content}}</div>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button plain type="success" @click="answerDetailVisible = false"></el-button>
        <el-button type="success" @click="answerDetailVisible = false"></el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog :title="report.paper_name + ''" :visible.sync="userReportsVisible">
      <div style="border-bottom: 1px solid #ccc;padding-bottom: 20px;margin-bottom: 20px;" v-for="(item,index) in report.data" key="index">
        <h2 style="font-size: 18px;font-weight: bold;margin: 0 0 10px;">{{item.title}}</h2>
        <div style="line-height: 2;" v-html="item.content"></div>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button plain type="success" @click="userReportsVisible = false"></el-button>
        <el-button type="success" @click="userReportsVisible = false"></el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="userInfoVisible">
      <h3 style="font-weight: bold;font-size: 18px;margin-bottom: 20px;"></h3>
      <el-form label-position="left" label-width="120px">
        <el-form-item label="">
          <el-input :value="info.name" readonly></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio v-model="info.man" :label="true" readonly></el-radio>
          <el-radio v-model="info.man" :label="false" readonly></el-radio>
        </el-form-item>
        <el-form-item label="">
          <el-input :value="info.birthday" readonly></el-input>
        </el-form-item>        
        <el-form-item label="">
          <el-input :value="info.parentName" readonly></el-input>
        </el-form-item>        
        <el-form-item label="">
          <el-input :value="info.age" readonly></el-input>
        </el-form-item>        
        <el-form-item label="">
          <el-input :value="info.relation" readonly></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input :value="info.job" readonly></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input :value="info.address" readonly></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input :value="info.phone" readonly></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <h3 class="info-h3"></h3>
      <div class="info-small-title">1.</div>
      <div v-for="item in ['','','','']"  style="margin-bottom: 10px;">
        <el-radio v-model="info.question1.first[0]" :label="item" readonly>{{item}}</el-radio>
      </div>
      <div class="info-small-title">2.</div>
      <div v-for="item in ['','']"  style="margin-bottom: 10px;">
        <el-radio v-model="info.question1.second[0]" :label="item" readonly>{{item}}</el-radio>
      </div>
      <div class="info-small-title">3.</div>
      <el-form label-position="left" label-width="120px">
        <el-form-item label="">
          <el-input :value="info.question1.third[0]" readonly></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input :value="info.question1.third[1]" readonly></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <h3 class="info-h3"></h3>
      <div class="info-small-title">1.</div>
      <div v-for="item in ['','','']"  style="margin-bottom: 10px;">
        <el-radio v-model="info.question2.first[0][0]" :label="item" readonly>{{item}}</el-radio>
      </div>
      <el-form label-position="left" label-width="160px">
        <el-form-item label="">
          <el-input :value="info.question2.first[1]" readonly></el-input>
        </el-form-item>
      </el-form>
      <div class="info-small-title">2.</div>
      <div v-for="item in ['','']"  style="margin-bottom: 10px;">
        <el-radio v-model="info.question2.second[0][0]" :label="item" readonly>{{item}}</el-radio>
      </div>
      <el-form label-position="left" label-width="160px">
        <el-form-item label="">
          <el-input :value="info.question2.second[1]" readonly></el-input>
        </el-form-item>
      </el-form>
      <div class="info-small-title"></div>
      <div v-for="item in ['','','']"  style="margin-bottom: 10px;">
        <el-radio v-model="info.question2.second[2][0]" :label="item" readonly>{{item}}</el-radio>
      </div>
      <div class="info-small-title">3.</div>
      <el-form label-position="left" label-width="160px">
        <el-form-item label="">
          <el-input :value="info.question2.third[0]" readonly></el-input>
        </el-form-item>
      </el-form>
      <div class="info-small-title"></div>
      <div v-for="item in ['','']"  style="margin-bottom: 10px;">
        <el-radio v-model="info.question2.third[1][0]" :label="item" readonly>{{item}}</el-radio>
      </div>
      <div class="info-small-title"></div>
      <div v-for="item in ['','']"  style="margin-bottom: 10px;">
        <el-radio v-model="info.question2.third[2][0]" :label="item" readonly>{{item}}</el-radio>
      </div>
      <div class="info-small-title"></div>
      <div v-for="item in ['','']"  style="margin-bottom: 10px;">
        <el-radio v-model="info.question2.third[3][0]" :label="item" readonly>{{item}}</el-radio>
      </div>
      <!--  -->
      <h3 class="info-h3"></h3>
      <div class="info-small-title">1.</div>
      <div class="info-small-title"></div>
      <div v-for="item in ['','','','','']"  style="margin-bottom: 10px;">
        <el-radio v-model="info.question3.first[0][0]" :label="item" readonly>{{item}}</el-radio>
      </div>
      <div class="info-small-title"></div>
      <div v-for="item in ['','','','','']"  style="margin-bottom: 10px;">
        <el-radio v-model="info.question3.first[1][0]" :label="item" readonly>{{item}}</el-radio>
      </div>
      <div class="info-small-title"></div>
      <div v-for="item in ['','','']"  style="margin-bottom: 10px;">
        <el-radio v-model="info.question3.first[2][0]" :label="item" readonly>{{item}}</el-radio>
      </div>
      <div class="info-small-title"></div>
      <div v-for="item in ['','','','']"  style="margin-bottom: 10px;">
        <el-radio v-model="info.question3.first[3][0]" :label="item" readonly>{{item}}</el-radio>
      </div>
      <div class="info-small-title"></div>
      <div v-for="item in ['','']"  style="margin-bottom: 10px;">
        <el-radio v-model="info.question3.first[4][0]" :label="item" readonly>{{item}}</el-radio>
      </div>
      <el-form label-position="left" label-width="160px">
        <el-form-item label="">
          <el-input :value="info.question3.second" readonly></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input :value="info.question3.third" readonly></el-input>
        </el-form-item>
      </el-form>
      <h3 class="info-h3"></h3>
      <el-checkbox-group v-model="info.question4">
        <el-checkbox v-for="item in ['','','','','','','','','']" :label="item" :key="item">{{item}}</el-checkbox>
      </el-checkbox-group>
      <h3 class="info-h3"></h3>
      <el-checkbox-group v-model="info.question5">
        <el-checkbox v-for="item in ['','','','','','','']" :label="item" :key="item">{{item}}</el-checkbox>
      </el-checkbox-group>
      <h3 class="info-h3"></h3>
      <el-checkbox-group v-model="info.question6">
        <el-checkbox v-for="item in ['','','','','','']" :label="item" :key="item">{{item}}</el-checkbox>
      </el-checkbox-group>
      <h3 class="info-h3"></h3>
      <el-input
        type="textarea"
        :autosize="{ minRows: 6, maxRows: 10}"
        placeholder=""
        v-model="info.question7">
      </el-input>
      <div style="padding-bottom: 20px;margin-bottom: 20px;"></div>
      <div style="border-bottom: 1px solid #ccc;"></div>
      <div slot="footer" class="dialog-footer">
        <el-button plain type="success" @click="userInfoVisible = false"></el-button>
        <el-button type="success" @click="userInfoVisible = false"></el-button>
      </div>
    </el-dialog>
	</div>
</template>

<script>
import API from '@/API'

export default {
  data() {
    return {
      tableData: [],
      userReportsVisible: false,
      answerDetailVisible: false,
      userInfoVisible: true,
      report:{
        paper_name: '',
        data: []
      },
      answer:{
        paper_name: '',
        items: []
      },
      info:{
        name: '',
        man: true,
        woman: false,
        birthday:'',
        parentName:'',
        age:'',
        relation:'',
        job:'',
        address:'',
        parentName:'',
        phone:'',
        question1:{
          first:[''],
          second:[''],
          third:['','']
        },
        question2:{
          first:[[''],'',['']],
          second:[[''],'',['']],
          third:['',[''],[''],['']]
        },
        question3:{
          first:[[''],[''],[''],[''],['']],
          second: '',
          third: ''
        },
        question4:[],
        question5:[],
        question6:''
      }
    }
  },
  async created(){
    let res = await API.getRecords()
    console.log(res)
    if( res.status != 200 ){
      this.$message({
        showClose: true,
        message: res.data,
        type: 'error'
      });
      return
    }
    const records = res.data
    for( let key in records ){
      this.tableData.push(records[key])
    }
    console.log(this.tableData)
  },
  methods: {
    async getReport(id){
      let res = await API.getReport(id)
      console.log(res)
      this.report = res.data
      this.userReportsVisible = true
    },
    async getAnswer(id){
      let res = await API.getAnswer(id)
      console.log(res)
      const data = res.data
      data.common = data.common.map( (item) =>{
        return {
          title: item.title,
          content: item.value[0] + ' --> ' + (item.options.indexOf(item.value[0])+1) + ''
        }
      })
      this.answer = {
        paper_name: data.paper_name,
        items: data.common
      }
      this.answerDetailVisible = true
    },
    async getInfo(id){
      let res = await API.getAnswer(id)
      console.log(res)
      this.info = res.data
      this.userInfoVisible = true
    }
  }
}
</script>

<style>
.records{
	margin: 20px auto;
  max-width: 750px;
}
.records .el-table th>.cell{
  color: #34495e;
  font-weight: bold;
}
.records .gray.el-button{
  color: #fff;
  background-color: #2a3f54;
  border-color: #223243;
}
.records .gray.el-button:hover{
  color: #fff;
  background-color: #0d141a;
  border-color: #000;
}
.records .mobile{
  display: none;
}
.records .el-card__body {
  padding-top: 10px;
}
.records .el-dialog__header {
  border-bottom: 1px solid #ccc;
}
.records .el-dialog{
  padding: 20px;
  width: 50%;
}
.records .el-dialog__headerbtn {
  top: 40px;
  right: 40px;
}
.records .el-dialog__body {
  padding: 30px 20px 10px;
}
.records .el-form-item {
  margin-bottom: 10px;
}
.records .info-h3{
  font-weight: bold;
  font-size: 18px;
  margin-bottom: 20px;
  margin-top: 20px;
}
.records .info-small-title{
  margin-bottom: 10px;
  margin-top: 6px;
}
.records .el-checkbox{
  display: block;
  margin-left: 0;
  margin-bottom: 5px;
}
@media screen and (max-width: 767px) {
	.records{
		margin: 15px;
	}
  .records .mobile{
    display: block;
  }
  .records .pc{
    display: none;
  }
  .records .el-card__body {
    padding: 10px;
  }
  .records .el-button+.el-button {
    margin-left: 0;
  }
  .records .el-dialog {
    width: calc(100% - 30px);
    margin: 15px;
    padding: 10px;
  }
  .records .el-dialog__headerbtn {
    top: 30px;
    right: 20px;
  }
}
</style>


